var staticRenderFns = [function (){var _vm=this,_c=_vm._self._c;return _c('section',[_c('h6',{staticClass:"heti-small"},[_vm._v("【一】")]),_c('p',[_vm._v("今天就是最后一次了，一定。")]),_c('p',[_vm._v(" Mina这么想着，将手机和钥匙装进提包，走出了家门。她按照习惯，从东南角进入Plaza Mayor，在广场的露天咖啡厅找了个位置坐下。她难得没有要一杯拿铁，而是冰美式；尽管在这样的天气里这么做，总有点伤害自己的感觉。 ")]),_c('p',[_vm._v(" 广场上不断有来去的熟悉面孔，他们或许是去北边的服装店，或许是去不远处的超市，总之跟自己的目的地没有干系。在等待咖啡的时间里，她收到了航空公司发来的确认邮件。 ")]),_c('p',[_vm._v(" 我有不想告别的心，她想。最好是悄无声息地乘上长途汽车，在机场丢掉关于这个国度的一切。 ")]),_c('p',[_vm._v(" 她笑了起来，拿起杯子；冰美式入口的凉爽感刺激了她的意志，催促她立刻行动。 ")]),_c('p',[_vm._v(" 打定主意，Mina就叫来了服务生，付好那杯咖啡的钱。这里距离车站不过十分钟的脚程，在营业时间赶到绰绰有余。她站起来，穿过了西侧的巷子，又连续走过几家甜品店。 ")]),_c('p',[_vm._v(" 即便是周末，也不会见到拥挤的人流。一群穿着小礼服的人站在街道另一侧的教堂外，悠闲地聊着天，手里还拿着酒杯。看来是传统婚礼。 ")]),_c('p',[_vm._v("这时Mina口袋里的手机震动了几下。是Sana发来的消息：")]),_c('p',[_vm._v("我有事情耽误了，不如晚上十点我们直接在酒吧见面，好吗？")]),_c('p',[_vm._v(" 等待又不是什么稀罕事；Mina嘲笑起自己，然后予以肯定的回答。要伪装得像从前一样，才能不被察觉自己的意图；她收起手机，和身边那些提着行李的人一起走进车站的大门。 ")]),_c('p',[_vm._v("购票的窗口仅开了一扇，倒是并没有排起长队。")]),_c('p',[_vm._v(" 上一次来到这里，还是和Sana一起，为了去Granada的旅行；一旦触碰到回忆，心绪难免会变得苦涩。Mina的眼眶微微有些湿润，好在售票窗口传来的声音打断了她的多愁善感： ")]),_c('p',[_vm._v("“Siguiente, por favor.（有请下一位。）”")]),_c('p',[_vm._v(" 她走上前，购买了三天后上午的大巴车票。这是参考了航班时间的结果，而航班选在那天的理由也很简单：Sana会在那天去Vienna，应该是下午出发。想必Sana会照常在接近十点钟才起床，然后急急忙忙收拾行李，好赶上那班列车。 ")]),_c('p',[_vm._v(" 把那条长长的车票夹进笔记本之后，Mina终于有了一种成就感，似乎自己做成了什么Sana期待之外的事情；毕竟，从来都是Sana支配着她的选择。 ")]),_c('p',[_vm._v("是的，从她们认识以来。")]),_c('p',[_vm._v("——或者说，从对Sana的一见钟情开始。")]),_c('p',[_vm._v(" 离开车站，Mina沿着向西的大道直行，在道路尽头那个公园的池塘边，注视了天鹅很久。然后，在夜幕降临之际，她拐过几个街角，终于踏入自己熟悉的餐厅。 ")]),_c('p',[_vm._v(" 在点餐的时候，Mina特地要了一瓶红酒，当作是一种庆祝；她看着表，距离和Sana约定的时间，还有两个小时。 ")]),_c('p',[_vm._v("自己的决意，当然要贯彻执行才行。")]),_c('p',[_vm._v(" 她沉默地用餐，间或以大口的吞咽灌下红酒，惹得侍者对她频频关注。在她喝下大半，脸颊也被染上颜色的时候，她收到了房东的答复，说可以在后天下午见面。 ")]),_c('p',[_vm._v(" 她头一次，对这个国度的人们的效率感到满意。一切进展都预示着更多成功的可能性，这点她心知肚明；当然，最大的不稳定因素就是Mina自己。 ")]),_c('p',[_vm._v(" 她再次饮尽杯中的液体，又将酒瓶倒空。照今天这个样子，晚上不出去玩似乎有点可惜；她自嘲起来，这样的紧要关头居然还有这种想法。 ")]),_c('p',[_vm._v(" 她结了帐，走出餐厅，剩了最后半杯酒在桌上。晚餐时间喝成这样子的可不多——自己的呼吸想必早就带着浓重的酒气。放慢的脚步踏着光滑的石子路，向着Plaza mayor走去；一路上有不少杂货店，都被Mina借作判断清醒程度的工具了。 ")]),_c('p',[_vm._v(" 看着广场旁酒吧的盛况，Mina这才想起，今晚有一场甲级联赛的对决；这项颇有魅力的运动，眼下她可是没有精力关注。不过，和Sana常去的酒吧并不提供电视转播，今晚肯定很冷清。 ")]),_c('p',[_vm._v("她用力推门，走了进去，在吧台前落座。")]),_c('p',[_vm._v(" 大概是喝过红酒的缘故，眼前这杯啤酒，此刻显得淡而无味。好在等待的时间并不长：几分钟后，Sana也落座了。 ")]),_c('p',[_vm._v(" “抱歉，”Sana解释说，“没注意时间，错过了一班车。没想到从Zaragoza回来的车班次那么少。对了，Patricia要到下周才会回来。” ")]),_c('p',[_vm._v("Mina没有应声。她根本不想听，只在心里重复着一句话。")]),_c('p',[_vm._v("今天就是最后一次了，一定。")])])
},function (){var _vm=this,_c=_vm._self._c;return _c('section',[_c('h6',{staticClass:"heti-small"},[_vm._v("【二】")]),_c('p',[_vm._v("是很熟悉的流程。")]),_c('p',[_vm._v(" 跟在Sana身后，走进她的公寓，看着她脱下大衣。时间是23:47，与往常没什么不同。 ")]),_c('p',[_vm._v("将外套和提包撇在一边，Mina自顾自在沙发上坐下，看着Sana。")]),_c('p',[_vm._v(" “Mina今天怎么感觉有点喝醉了，平常酒量不都很不错吗？”Sana关切地走近，摸了摸她的脸颊。 ")]),_c('p',[_vm._v(" 她屏息看了Sana几秒钟，拉过Sana的领子，以细弱的身躯承担Sana倒下的重量——然后，反过来将她压在身下；她的鼻尖划过Sana的下颌线，稍稍停留，随即向更下方进发。 ")]),_c('p',[_vm._v(" 如果心理越压抑，释放的方式就更偏激吧——自己在Sana眼里大概也只是个疯子：只会在善良的朋友身上追寻刺激的、可耻的疯子。 ")]),_c('p',[_vm._v(" 她听到Sana在自己耳边笑了起来，“Mina怎么这么急。”一边连鼻息都打在自己身上。 ")]),_c('p',[_vm._v(" 她并不作声，只是解开Sana衬衫的扣子，用舌尖将干燥的胸部肌肤变得潮湿，膝盖稍稍用力，分开Sana的双腿。Sana真的是有备而来：在这样萧瑟的秋夜里穿着超短裙，不得不说是一种放肆。 ")]),_c('p',[_vm._v(" 她的右手从大腿摸进去，惹得Sana哼了一声，大概是有些凉；Mina顺势将短裙推到腰间，接着向后，由背部下行来到臀部。Sana很配合地扭动腰肢，她的手臂环住Mina的脖颈，双腿也围在她的腰间。 ")]),_c('p',[_vm._v(" Sana的体温比自己想象的要热情，Mina想，然后松开了揉捏乳房的左手。她的手指顺着细腻的肌肤向下，去抚摸Sana的阴唇。 ")]),_c('p',[_vm._v(" 湿滑的触感染上指尖的时候，Mina突然好奇，自己究竟想在对方身上获得什么奇迹；她停下动作，扬起头去看Sana的脸。昏暗的灯光中，她只能看到她发红的面庞，还有陡然凑近的双眼。 ")]),_c('p',[_vm._v("“Mina怎么了？”")]),_c('p',[_vm._v(" Sana的关心倒是一如往常；Mina看着已然坐在自己身前的她，倏然觉得某种信念在崩塌。她吸了一口气，倾身向前，拥住纤细的Sana。 ")]),_c('p',[_vm._v("Sana愣了一下，然后予以回应，紧紧地抱住了她。")]),_c('p',[_vm._v(" 拥抱是很久违了；那是她们还保持着纯洁的友谊的过去，在异国互相支撑的力量。被揭开的回忆让Mina感到割裂，几乎要涌出泪水。只是，安静的时间总会在心里留下漫长的痕迹；她们沉默了不知多久，或许双方都是为了忍耐眼泪。 ")]),_c('p',[_vm._v("不过，街上传来的吵闹声打破了这种寂静。")]),_c('p',[_vm._v(" Mina清醒过来，放开Sana，为她拉好领子，甚至想把她衣服的褶皱展平。然后，她站了起来，开始摸索着拿起自己的东西；回忆让她有些胆怯，不知道该怎么面对Sana——她的慌乱一如三个月前，这种复杂的关系开始的那刻。 ")]),_c('p',[_vm._v(" Sana看出了她要逃离的意图，似乎感到了冒犯；在Mina夺门而出之前，她站在门口拦住了去路。 ")]),_c('p',[_vm._v("“Mina是什么意思？”")]),_c('p',[_vm._v(" Sana的声音带着颤抖，Mina想道，那或许是种愤怒。她正暗自庆幸黑暗可以隐藏起自己的表情的时候，Sana打开了玄关处的灯，以衣衫不整的姿态呈现在她眼前。 ")]),_c('p',[_vm._v(" Mina偏过头去，不再看她。但是，Sana的吻追过来了：一开始是落在脸颊，随后来到嘴角。会让自己发疯的味道萦绕在鼻尖，Mina很快就晕头转向——下一秒，就被Sana推到墙上。 ")]),_c('p',[_vm._v(" 她感到有点痛，想要离冰冷的墙面远一点，却被Sana的手臂强硬地制止。Sana抓过她的双手，放在自己的胸前；那是她暴露在外的上半身的一部分，除去湿润的触感，还能瞥见发红的迹象。 ")]),_c('p',[_vm._v(" Mina以模糊且朦胧的视线看着眼前的Sana，任由她把两根手指放进自己的嘴里，然后心甘情愿地配合她的搅动，送上柔软温热的舔舐。她能感受到Sana解开了自己的腰带，褪下裤子，让皮肤接触湿冷的空气。她不禁抖了一下。 ")]),_c('p',[_vm._v(" 于是她向前靠去，右手摸到Sana背后短裙的拉链，想要拉下来。她还没用力，Sana的手指先一步碰到了她的阴蒂，轻轻刮了两下；她忍住喊叫的冲动，扶着Sana腰间的左手用力抓了一下。 ")]),_c('p',[_vm._v(" Sana笑了起来，接着去追逐她逃跑的胸口，一边手指努力起来，带着响亮的水声；Mina的喘息随着手指抖动的速度加快，最后叫了起来。 ")]),_c('p',[_vm._v(" 在一阵连续而嘶哑的叫声过后，就只剩下战栗的身躯与静谧的空气。Mina伏在Sana的肩膀上，以残存的力气抱紧她的肩头。 ")]),_c('p',[_vm._v("“去洗澡吧。”Sana这样说。")]),_c('p',[_vm._v(" Mina坐在浴缸里，抱着自己的膝盖，意识有些呆滞。她看着扎起头发、正在脱掉内衣的Sana，回忆起三个月前的那个夜晚。 ")]),_c('p',[_vm._v(" 扭曲的关系确实就是从那一刻开始的，对此两个人都心知肚明。只是，自己的臆想中的Sana，一定与真实的她不同。Mina就是抱着这种念头，才在无数次的后悔中留在原地。 ")]),_c('p',[_vm._v(" 这时，Sana的身影和记忆分离开来了；她走过来，坐在了自己身前。Mina凑上前去，紧拥着她，亲吻她光洁的后背，然后左手的两指夹住她的乳尖。Sana抓起她的右手，借她的手指刮蹭自己的阴部；Mina的牙齿轻轻咬在颈后侧，双乳压在Sana的背部摩擦。 ")]),_c('p',[_vm._v(" Sana叫起来了；她一向无所顾忌，所以叫起来也更好听，Mina心里这么评价着，右手更勤奋地工作起来。第一次做的时候，Mina还对她这种热烈的反应感到惊讶；毕竟那个开始可是毫无征兆，谈不上一丝浪漫的氛围。 ")]),_c('p',[_vm._v(" 陷入回忆当然是一种分心；Sana不满地哼起来，Mina随即加快了速度和力度。Sana的手臂向后绕紧她的脖子，身体猛烈地震颤起来，最后以高亢的独奏结束。 ")]),_c('p',[_vm._v(" 她扭过头来和Mina接吻；Mina自作多情地想，她的动作或许带有某种感情。在为Sana清洗头发的时候，她听到了Sana的问话： ")]),_c('p',[_vm._v("“今晚Mina要留下来吗？”")]),_c('p',[_vm._v(" 她没有答话，但是决定照做了；这还是三个月来的第一次。当她裹着浴巾，任由Sana给她吹干头发的时候，她注意到了卧室里的时钟。 ")]),_c('p',[_vm._v("已经是凌晨两点半。")]),_c('p',[_vm._v("“夜晚比我想象得要短呢。”她感叹道。")]),_c('p',[_vm._v("“还可以再长一点。”Sana不怀好意地笑了笑，顺手打了她的屁股。")]),_c('p',[_vm._v(" 接着，她们爬上床去；Mina很自觉地塌下腰，把两腿之间的位置暴露在Sana面前。两人沙哑的叫声交替响起，持续到身上都布满对方的痕迹。 ")]),_c('p',[_vm._v(" ——再过一会儿，连寻欢作乐的学生们都要回家了；Mina这样想着，换了个姿势躺好。 ")]),_c('p',[_vm._v("Sana背对着她，似乎是睡熟了。")]),_c('p',[_vm._v(" Mina看着她散乱的头发，有了一丝怀念的伤感。她轻轻地贴近Sana的背，闭上了双眼。 ")]),_c('p',[_vm._v("——如果我的世界没有你，会是什么样子呢？")]),_c('p',[_vm._v("那是，现在精神疲惫的Mina无论如何也描绘不出的景象。")]),_c('p',[_vm._v("Sana，对不起，她在心里说。")]),_c('p',[_vm._v("可是，我不会向你道别。")])])
