    <div class="arti">
      <article class="entry heti" ref="article">
        <h4>我不会向你道别</h4>
        <div class="heti-meta heti-small">全四章（已完结27/12/2020）</div>
        <p></p>

        <section>
          <h6 class="heti-small">【一】</h6>

          <p>今天就是最后一次了，一定。</p>
          <p>
            Mina这么想着，将手机和钥匙装进提包，走出了家门。她按照习惯，从东南角进入Plaza
            Mayor，在广场的露天咖啡厅找了个位置坐下。她难得没有要一杯拿铁，而是冰美式；尽管在这样的天气里这么做，总有点伤害自己的感觉。
          </p>
          <p>
            广场上不断有来去的熟悉面孔，他们或许是去北边的服装店，或许是去不远处的超市，总之跟自己的目的地没有干系。在等待咖啡的时间里，她收到了航空公司发来的确认邮件。
          </p>
          <p>
            我有不想告别的心，她想。最好是悄无声息地乘上长途汽车，在机场丢掉关于这个国度的一切。
          </p>
          <p>
            她笑了起来，拿起杯子；冰美式入口的凉爽感刺激了她的意志，催促她立刻行动。
          </p>
          <p>
            打定主意，Mina就叫来了服务生，付好那杯咖啡的钱。这里距离车站不过十分钟的脚程，在营业时间赶到绰绰有余。她站起来，穿过了西侧的巷子，又连续走过几家甜品店。
          </p>
          <p>
            即便是周末，也不会见到拥挤的人流。一群穿着小礼服的人站在街道另一侧的教堂外，悠闲地聊着天，手里还拿着酒杯。看来是传统婚礼。
          </p>
          <p>这时Mina口袋里的手机震动了几下。是Sana发来的消息：</p>
          <p>我有事情耽误了，不如晚上十点我们直接在酒吧见面，好吗？</p>
          <p>
            等待又不是什么稀罕事；Mina嘲笑起自己，然后予以肯定的回答。要伪装得像从前一样，才能不被察觉自己的意图；她收起手机，和身边那些提着行李的人一起走进车站的大门。
          </p>
          <p>购票的窗口仅开了一扇，倒是并没有排起长队。</p>
          <p>
            上一次来到这里，还是和Sana一起，为了去Granada的旅行；一旦触碰到回忆，心绪难免会变得苦涩。Mina的眼眶微微有些湿润，好在售票窗口传来的声音打断了她的多愁善感：
          </p>
          <p>“Siguiente, por favor.（有请下一位。）”</p>
          <p>
            她走上前，购买了三天后上午的大巴车票。这是参考了航班时间的结果，而航班选在那天的理由也很简单：Sana会在那天去Vienna，应该是下午出发。想必Sana会照常在接近十点钟才起床，然后急急忙忙收拾行李，好赶上那班列车。
          </p>
          <p>
            把那条长长的车票夹进笔记本之后，Mina终于有了一种成就感，似乎自己做成了什么Sana期待之外的事情；毕竟，从来都是Sana支配着她的选择。
          </p>
          <p>是的，从她们认识以来。</p>
          <p>——或者说，从对Sana的一见钟情开始。</p>
          <p>
            离开车站，Mina沿着向西的大道直行，在道路尽头那个公园的池塘边，注视了天鹅很久。然后，在夜幕降临之际，她拐过几个街角，终于踏入自己熟悉的餐厅。
          </p>
          <p>
            在点餐的时候，Mina特地要了一瓶红酒，当作是一种庆祝；她看着表，距离和Sana约定的时间，还有两个小时。
          </p>
          <p>自己的决意，当然要贯彻执行才行。</p>
          <p>
            她沉默地用餐，间或以大口的吞咽灌下红酒，惹得侍者对她频频关注。在她喝下大半，脸颊也被染上颜色的时候，她收到了房东的答复，说可以在后天下午见面。
          </p>
          <p>
            她头一次，对这个国度的人们的效率感到满意。一切进展都预示着更多成功的可能性，这点她心知肚明；当然，最大的不稳定因素就是Mina自己。
          </p>
          <p>
            她再次饮尽杯中的液体，又将酒瓶倒空。照今天这个样子，晚上不出去玩似乎有点可惜；她自嘲起来，这样的紧要关头居然还有这种想法。
          </p>
          <p>
            她结了帐，走出餐厅，剩了最后半杯酒在桌上。晚餐时间喝成这样子的可不多——自己的呼吸想必早就带着浓重的酒气。放慢的脚步踏着光滑的石子路，向着Plaza
            mayor走去；一路上有不少杂货店，都被Mina借作判断清醒程度的工具了。
          </p>
          <p>
            看着广场旁酒吧的盛况，Mina这才想起，今晚有一场甲级联赛的对决；这项颇有魅力的运动，眼下她可是没有精力关注。不过，和Sana常去的酒吧并不提供电视转播，今晚肯定很冷清。
          </p>
          <p>她用力推门，走了进去，在吧台前落座。</p>
          <p>
            大概是喝过红酒的缘故，眼前这杯啤酒，此刻显得淡而无味。好在等待的时间并不长：几分钟后，Sana也落座了。
          </p>
          <p>
            “抱歉，”Sana解释说，“没注意时间，错过了一班车。没想到从Zaragoza回来的车班次那么少。对了，Patricia要到下周才会回来。”
          </p>
          <p>Mina没有应声。她根本不想听，只在心里重复着一句话。</p>
          <p>今天就是最后一次了，一定。</p>
        </section>

        <section>
          <h6 class="heti-small">【二】</h6>
          <p>是很熟悉的流程。</p>
          <p>
            跟在Sana身后，走进她的公寓，看着她脱下大衣。时间是23:47，与往常没什么不同。
          </p>
          <p>将外套和提包撇在一边，Mina自顾自在沙发上坐下，看着Sana。</p>
          <p>
            “Mina今天怎么感觉有点喝醉了，平常酒量不都很不错吗？”Sana关切地走近，摸了摸她的脸颊。
          </p>
          <p>
            她屏息看了Sana几秒钟，拉过Sana的领子，以细弱的身躯承担Sana倒下的重量——然后，反过来将她压在身下；她的鼻尖划过Sana的下颌线，稍稍停留，随即向更下方进发。
          </p>
          <p>
            如果心理越压抑，释放的方式就更偏激吧——自己在Sana眼里大概也只是个疯子：只会在善良的朋友身上追寻刺激的、可耻的疯子。
          </p>
          <p>
            她听到Sana在自己耳边笑了起来，“Mina怎么这么急。”一边连鼻息都打在自己身上。
          </p>
          <p>
            她并不作声，只是解开Sana衬衫的扣子，用舌尖将干燥的胸部肌肤变得潮湿，膝盖稍稍用力，分开Sana的双腿。Sana真的是有备而来：在这样萧瑟的秋夜里穿着超短裙，不得不说是一种放肆。
          </p>
          <p>
            她的右手从大腿摸进去，惹得Sana哼了一声，大概是有些凉；Mina顺势将短裙推到腰间，接着向后，由背部下行来到臀部。Sana很配合地扭动腰肢，她的手臂环住Mina的脖颈，双腿也围在她的腰间。
          </p>
          <p>
            Sana的体温比自己想象的要热情，Mina想，然后松开了揉捏乳房的左手。她的手指顺着细腻的肌肤向下，去抚摸Sana的阴唇。
          </p>
          <p>
            湿滑的触感染上指尖的时候，Mina突然好奇，自己究竟想在对方身上获得什么奇迹；她停下动作，扬起头去看Sana的脸。昏暗的灯光中，她只能看到她发红的面庞，还有陡然凑近的双眼。
          </p>
          <p>“Mina怎么了？”</p>
          <p>
            Sana的关心倒是一如往常；Mina看着已然坐在自己身前的她，倏然觉得某种信念在崩塌。她吸了一口气，倾身向前，拥住纤细的Sana。
          </p>
          <p>Sana愣了一下，然后予以回应，紧紧地抱住了她。</p>
          <p>
            拥抱是很久违了；那是她们还保持着纯洁的友谊的过去，在异国互相支撑的力量。被揭开的回忆让Mina感到割裂，几乎要涌出泪水。只是，安静的时间总会在心里留下漫长的痕迹；她们沉默了不知多久，或许双方都是为了忍耐眼泪。
          </p>
          <p>不过，街上传来的吵闹声打破了这种寂静。</p>
          <p>
            Mina清醒过来，放开Sana，为她拉好领子，甚至想把她衣服的褶皱展平。然后，她站了起来，开始摸索着拿起自己的东西；回忆让她有些胆怯，不知道该怎么面对Sana——她的慌乱一如三个月前，这种复杂的关系开始的那刻。
          </p>
          <p>
            Sana看出了她要逃离的意图，似乎感到了冒犯；在Mina夺门而出之前，她站在门口拦住了去路。
          </p>
          <p>“Mina是什么意思？”</p>
          <p>
            Sana的声音带着颤抖，Mina想道，那或许是种愤怒。她正暗自庆幸黑暗可以隐藏起自己的表情的时候，Sana打开了玄关处的灯，以衣衫不整的姿态呈现在她眼前。
          </p>
          <p>
            Mina偏过头去，不再看她。但是，Sana的吻追过来了：一开始是落在脸颊，随后来到嘴角。会让自己发疯的味道萦绕在鼻尖，Mina很快就晕头转向——下一秒，就被Sana推到墙上。
          </p>
          <p>
            她感到有点痛，想要离冰冷的墙面远一点，却被Sana的手臂强硬地制止。Sana抓过她的双手，放在自己的胸前；那是她暴露在外的上半身的一部分，除去湿润的触感，还能瞥见发红的迹象。
          </p>
          <p>
            Mina以模糊且朦胧的视线看着眼前的Sana，任由她把两根手指放进自己的嘴里，然后心甘情愿地配合她的搅动，送上柔软温热的舔舐。她能感受到Sana解开了自己的腰带，褪下裤子，让皮肤接触湿冷的空气。她不禁抖了一下。
          </p>
          <p>
            于是她向前靠去，右手摸到Sana背后短裙的拉链，想要拉下来。她还没用力，Sana的手指先一步碰到了她的阴蒂，轻轻刮了两下；她忍住喊叫的冲动，扶着Sana腰间的左手用力抓了一下。
          </p>
          <p>
            Sana笑了起来，接着去追逐她逃跑的胸口，一边手指努力起来，带着响亮的水声；Mina的喘息随着手指抖动的速度加快，最后叫了起来。
          </p>
          <p>
            在一阵连续而嘶哑的叫声过后，就只剩下战栗的身躯与静谧的空气。Mina伏在Sana的肩膀上，以残存的力气抱紧她的肩头。
          </p>
          <p>“去洗澡吧。”Sana这样说。</p>
          <p>
            Mina坐在浴缸里，抱着自己的膝盖，意识有些呆滞。她看着扎起头发、正在脱掉内衣的Sana，回忆起三个月前的那个夜晚。
          </p>
          <p>
            扭曲的关系确实就是从那一刻开始的，对此两个人都心知肚明。只是，自己的臆想中的Sana，一定与真实的她不同。Mina就是抱着这种念头，才在无数次的后悔中留在原地。
          </p>
          <p>
            这时，Sana的身影和记忆分离开来了；她走过来，坐在了自己身前。Mina凑上前去，紧拥着她，亲吻她光洁的后背，然后左手的两指夹住她的乳尖。Sana抓起她的右手，借她的手指刮蹭自己的阴部；Mina的牙齿轻轻咬在颈后侧，双乳压在Sana的背部摩擦。
          </p>
          <p>
            Sana叫起来了；她一向无所顾忌，所以叫起来也更好听，Mina心里这么评价着，右手更勤奋地工作起来。第一次做的时候，Mina还对她这种热烈的反应感到惊讶；毕竟那个开始可是毫无征兆，谈不上一丝浪漫的氛围。
          </p>
          <p>
            陷入回忆当然是一种分心；Sana不满地哼起来，Mina随即加快了速度和力度。Sana的手臂向后绕紧她的脖子，身体猛烈地震颤起来，最后以高亢的独奏结束。
          </p>
          <p>
            她扭过头来和Mina接吻；Mina自作多情地想，她的动作或许带有某种感情。在为Sana清洗头发的时候，她听到了Sana的问话：
          </p>
          <p>“今晚Mina要留下来吗？”</p>
          <p>
            她没有答话，但是决定照做了；这还是三个月来的第一次。当她裹着浴巾，任由Sana给她吹干头发的时候，她注意到了卧室里的时钟。
          </p>
          <p>已经是凌晨两点半。</p>
          <p>“夜晚比我想象得要短呢。”她感叹道。</p>
          <p>“还可以再长一点。”Sana不怀好意地笑了笑，顺手打了她的屁股。</p>
          <p>
            接着，她们爬上床去；Mina很自觉地塌下腰，把两腿之间的位置暴露在Sana面前。两人沙哑的叫声交替响起，持续到身上都布满对方的痕迹。
          </p>
          <p>
            ——再过一会儿，连寻欢作乐的学生们都要回家了；Mina这样想着，换了个姿势躺好。
          </p>
          <p>Sana背对着她，似乎是睡熟了。</p>
          <p>
            Mina看着她散乱的头发，有了一丝怀念的伤感。她轻轻地贴近Sana的背，闭上了双眼。
          </p>
          <p>——如果我的世界没有你，会是什么样子呢？</p>
          <p>那是，现在精神疲惫的Mina无论如何也描绘不出的景象。</p>
          <p>Sana，对不起，她在心里说。</p>
          <p>可是，我不会向你道别。</p>
        </section>

        <section>
          <h6>【三】</h6>
          <p>阳光明媚的话，一定是有个透光性良好的窗帘。</p>
          <p>
            Sana在心中哀鸣，然后给自己蒙上被子。她感受到有人在床边走动，于是又探出头来看看。
          </p>
          <p>“醒了？”是Mina的声音。她已经穿戴整齐，准备打开卧室的门。</p>
          <p>Sana坐起身，张开臂膀，以撒娇回应。</p>
          <p>Mina只是看了她一眼，然后就走了出去。</p>
          <p>脚步声过后，她听到大门关上的响声。</p>
          <p>“都不跟我说再见，”Sana有些恼火，“就几个字而已。”</p>
          <p>
            她又倒回床上，阖上眼想要再补一觉。不过这种事总是不能如愿：她的手机铃声响起来了，而且是在客厅里。
          </p>
          <p>
            急急忙忙找到了睡衣穿上，光着脚跳到沙发旁边，在茶几上看到了显示未接来电的手机。
          </p>
          <p>
            是Dolores，小组活动的同学。她又跳回卧室钻进被子里，用手机回拨过去。
          </p>
          <p>
            原来是组长更改了活动计划。事实上，也不过是比原计划晚一天出发而已；她挂掉电话，拿着浴巾去洗了澡。
          </p>
          <p>
            那就可以把一天的事情分给两天做，Sana想，然后给Mina发了消息。等待回复的时间里，她精心化好妆，对着镜子搭配了衣服。
          </p>
          <p>“今天没有时间看电影，我还有事要做。”</p>
          <p>Mina这样回复道。</p>
          <p>
            好吧，Sana叹了口气，接着用WhatsApp翻找可能有空的朋友。Gloria表示自己正打算参加系里组织的活动，可以等她一起；可那要等到接近下午六点，等待的时间就很久了。
          </p>
          <p>算了，那就一个人去吧。</p>
          <p>
            Sana并不经常一个人行动，大概是总有拉上别人的习惯；她换好马丁靴，戴上贝雷帽，走下楼梯。
          </p>
          <p>
            午后的阳光确实温暖，只是让人有些睁不开眼睛。她后悔没戴个墨镜，或者选个有帽檐的帽子，只好用手遮挡日光，继续前进。
          </p>

          <p>
            如果能更早吃完饭，或许赶得上下午场的电影，然后在晚饭后去散步；去Tormes河边的公园慢跑也相当不错。
          </p>

          <p>
            她走进街对面的咖啡厅，要了一杯Macchiato和奶油内馅的Napolitana，在临街的窗户旁坐下。这算是难得的放纵了；那些奶油的热量足够Sana后悔两天时间。
          </p>

          <p>
            和自己不同，Mina总是一个人生活的；很安静的，一个人生活。大家一开始对她抱持这样的印象：安静、温柔，又很细致。看起来她从来不会拒绝别人提出的要求，总是温和地予以回应。
          </p>

          <p>回忆被打断了。Sana谢过侍者，拿起刀叉对准甜品。</p>

          <p>
            Mina和自己有很大不同，这是与Mina相处的最初认识。要想获得别人的好感，就要为此付出努力，无论是外貌还是言谈&mdash;&mdash;长久以来，Sana就是以这种想法要求自己，不断地回应他人的期望的；但Mina不一样，她对别人的好感没有期待或欲望，并不会表现出多余的热情。
          </p>

          <p>
            &ldquo;这样很难被别人喜欢啊，Mina，&rdquo;Sana还记得自己这样说过，&ldquo;如果你实际的行为和大家对你的期待不太一致，别人会很失望的。&rdquo;
          </p>

          <p>
            当时Mina究竟是如何作答，已经记不清楚了。事实上，相识以后近半年的时间里，两人的交集并不算密切，仅仅由于是同胞的缘故才偶有来往。那时候Sana对Mina的人气就有所耳闻，不过她冷淡的态度更是出名；没有多久，众人就对她失去了兴趣。
          </p>

          <p>
            &ldquo;Esp&iacute;ritu solitario y
            libre（孤独而自由的灵魂）&rdquo;，是Sana的前男友Carlos对Mina的形容。她的确是孤独的没错，Sana想，所以才让人非常想要靠近。她收起侍者拿来的找零，走出了咖啡厅。
          </p>

          <p>
            在去电影院的路上，Sana感觉有点热，于是脱掉外套，拿在手里。秋季的温度总是这样的：不论穿几层都不太合适，就连偶尔吹来的风都忽冷忽热。她在人行道的红灯前等候，某种熟悉又令人厌恶的烟雾就在行人中散开了。
            >
          </p>

          <p>她皱了皱鼻子，然后加快步伐，尽量走在前面。</p>

          <p>
            坐在影厅里的时候，她想起过去Mina冷淡的反应：静默地看完，神色如常地给自己递上纸巾。
          </p>

          <p>
            这个人会有感情吗？Sana自认对她不甚了解。虽然Mina不会拒绝自己提出的要求，但她索取回报的方式也足够直接&mdash;&mdash;Sana想到这个，耳朵微微红了起来。她想起了夜里发生的事；好在电影开始了，黑暗中，只有前方的光才有吸引力。
          </p>

          <p>
            这部由她母语拍摄的电影，剧情并不难理解，倒是为她活跃的大脑留出了空间。和Mina关系变得密切，是大约一年前的事；那时候她与Carlos吵架很频繁，只想换个地方透透气。
          </p>

          <p>
            能与同胞出行总归要方便一些，因此她们成为了旅伴，一同前往Alcara。在那次三天两夜的旅途中，Sana回避了感情相关的话题，最后做出了分手的决定。
          </p>

          <p>
            &mdash;&mdash;一定是看着Mina一个人的身影，有了向往。Sana是这么说服自己的；正如她每一个看似大胆放纵却瞻前顾后的选择一样，明智从来不站在她这一边，后果自然也无法估量。
          </p>

          <p>
            影院里黑了下去，重又亮起，似乎是某个场景的切换；但是回忆却没有放过Sana。
          </p>

          <p>她闭上眼睛。那是三个月前。</p>

          <p>
            盛夏的夜晚总是伴随着狂野和冲动，不过不属于Sana；Mina牵着她的手，走在前面。她们刚刚脱离愤怒的某个人，此刻Sana依然内心忐忑。
          </p>

          <p>
            Mina并不与她言语，只是执着地向前，再向前；她们与醉酒狂欢的学生擦身而过。她不自觉地抓紧Mina的手，直到在Mina的沙发上落座才松开。
          </p>

          <p>Mina倒了一杯水，放在她的手里。</p>

          <p>&ldquo;这种事倒是也可以依赖我。&rdquo;Mina说。</p>

          <p>
            可是，Sana想，你会想要什么回报？她还没来得及投出询问的目光，就感受到Mina的拥抱。那只是个开始，接着，她的耳朵就在Mina的鼻息中潮湿起来。她接受了，完全没有反抗。
          </p>

          <p>&mdash;&mdash;Mina，如果向你坦白我喜欢你，你会厌恶得想离开吗？</p>

          <p>很努力地，从记忆中寻找哪怕一丝一毫Mina有所回应的迹象。</p>

          <p>
            承认吧，她对自己说。昨晚那些Mina反常却温暖的举动，都是你在自作多情。
          </p>

          <p>Sana终于失望地落下泪来。</p>
        </section>

        <section>
          <h6>【四】</h6>
          <p>告别，总是比预想中要艰难。</p>

          <p>
            Mina不是第一次得出这个结论；在离开家乡，独自来到这个地球另一端的国度之时，她就体会过这种情感了。不过今时今日确实有些不同：她还是头一次带着强烈的悔恨面对自己的过去。
          </p>

          <p>
            她正在Plaza
            Mayor中央，一边喂鸽子，一边回忆在这里度过的一年半时光。明天中午，Mina就将搭上回国的航班，以漫长的跋涉换取故国的气息。
          </p>

          <p>
            其实并没有很怀念故土，毕竟她从来都用逃避的姿态远离过去；30个小时的旅途，也不会迎来柳暗花明的喜乐。况且，她没有告知家人自己的返程，自然也不会有什么人来接她。
          </p>

          <p>
            对于Mina来说，保持自己的方向当然是最重要的；及时纠正偏离的航道，时刻保持专注。我已经错了太久了，她想，虽然自己常常过分投入某种趣味，但对某个人如此着迷，还是第一次。
          </p>

          <p>因为是Sana。</p>

          <p>是会为了讨人喜欢而展现好意的Sana。</p>

          <p>
            她在石凳上坐下，目光集中在脚下的地面，打算将这场反省延续至与房东的会面；既然行李已经收拾妥当，回忆当然也刻不容缓。
          </p>

          <p>&mdash;&mdash;与你初见以来，已经一年半的时间。</p>

          <p>
            相遇的季节依然历历在目：早春时节，欧洲大陆和煦的西风没能如期而至，伊比利亚半岛的潮湿阴冷也迟迟不肯告别。这里的低温，让刚刚告别了樱花景象的Mina有些不适应；好在她一向乐于感受寒冷，更乐于感受陌生的环境。
          </p>

          <p>
            很快，她迎来一个短暂的假期，得以在繁杂的课程开始前去海边透口气。没有选择地中海西岸，而是反其道而行之，来到北大西洋东岸的Mina，就是在朝圣之路的终点与Sana相遇的。
          </p>

          <p>
            并不是教徒，只是以观光的心态；Mina走在大教堂前的广场，在取景器的后方选好了角度。
          </p>

          <p>
            然后，她几次按下快门。在她检视成果的时候，方才结束了教堂游览的Sana，就出现在她的视野里：茶色的卷发和明亮的双眸，瞬间将Mina内心的火花点燃。或许是阴云密布的日子里太过乏味，急需调剂的她悄悄转过镜头，拍下了微微扬起头、小跳着步子前进的Sana的侧影。
          </p>

          <p>
            接着，Sana走向广场的东侧&mdash;&mdash;正是Mina所在的那一侧。Mina停留在原地，决意用目光来记录她迎面而来的时刻：飞快的脚步带起微风，将发尾微微卷起；她的视线迅速扫过Mina身侧，大衣的衣角堪堪划过两人间的空气。
          </p>

          <p>
            &mdash;&mdash;若回忆能就此停止，那真是再好不过。可惜Mina的妄想与现实无关：偶然却又必然的相遇带来了生活的转机，或者，叫烦恼更恰当。回到学校的她很快再度与Sana相见，随即了解到Carlos正是Sana远渡重洋来到这个国度的理由。这倒是与我无关，Mina想，除了内心隐隐作痛。
          </p>

          <p>
            她自认是相当自闭的人，因此大多数时候都不参与专为留学生组织的活动，只是偶尔会去哪里独自喝一杯。但是，毕竟她们身处同一座城市&mdash;&mdash;还是这样不过十二万人的小城&mdash;&mdash;总会有无数机会碰到面。大概第五次，在酒吧遇到Sana的时候，Mina对无处不在的心痛感到疲惫了；那时她抱着脑袋，然后听到了Sana的那句话：
          </p>

          <p>
            &ldquo;如果你实际的行为和大家对你的期待不太一致，别人会很失望的。&rdquo;
          </p>

          <p>
            真是非常可笑的善心，Mina在心里嘲讽。在沉默了接近半分钟后，她扬起下巴，带着挑衅的目光反问道：
          </p>

          <p>&ldquo;是大家的期待呢，还是Sana对我的期待呢？&rdquo;</p>

          <p>
            Sana似乎是愣了一下，没有答话。这种近似狡辩的行为经常性地，能取得这样的效果；事实上，不光是狡辩，在任何议题中Mina都能以奇怪的逻辑自洽占据上风。也许她不会回答了，Mina这么想着，打算离开。
          </p>

          <p>
            &ldquo;就当是我的期待好了，Mina，&rdquo;Sana拉住她的手肘，恳切的语调很是让人动容，&ldquo;你不能总是这个样子。&rdquo;
          </p>

          <p>&ldquo;这算是以友人身份的劝诫吗？&rdquo;Mina眯起眼睛，问道。</p>

          <p>
            毋需否认，这奇怪的对话只会在Mina和Sana之间成立；不过，Mina的确用行动回应了Sana的期待。她演起戏来是一把好手；无论是高中时代，还是眼下身处海外。自从Alcara回来后，她很积极地活跃在Sana身边，以亲友的姿态为她排忧解难。
          </p>

          <p>
            演戏演得越久，越像是现实，最后整个人与角色浑然一体。向来蔑视别人评判的Mina，开始忘却过去的信念，陷入同龄人普遍的焦虑之中。好在这种日子没有很久，在Carlos数次激怒Sana之后，他们终于分了手；当晚，Sana向Mina哭诉了许久，细数自己的投入与付出。
          </p>

          <p>
            &mdash;&mdash;就是那时起，Mina意识到自己失去了什么。她看着入睡的Sana，对六个多月的扮演经历做出了反思：要保持距离，她做出了这样的决定。她从Sana的生活中撤退，找回从前的爱好，以冷淡的态度面对Sana的亲近。
          </p>

          <p>
            如果一切如她所愿，离开当然不至于像今天这样痛苦；大约三个月前，Carlos的暴力伤害到Sana身体那时起，Mina充分地了解了自己的嫉妒与愤怒，然后将怒火以越界的形式表达出来。
          </p>

          <p>
            那么，对Sana来说，自己的陪伴就是要以夜晚的时间来交换的吧；如果她知晓了自己的不告而别，这份屈辱感会否转变成怨恨呢？
          </p>

          <p>
            Mina无法得出答案。就用好奇来承托对Sana的想念吧，她想，我会把自己的嫉妒与占有欲都埋葬在这片土地。
          </p>

          <p>她长出一口气。</p>

          <p>我不会向你道别。</p>
        </section>

        <p></p>
        <p style="text-align: center">-FIN-</p>

